<template>
  <div id="app">
    <!--  -->
    <router-view v-wechat-title='$route.meta.title' v-if="isRouterAlive"></router-view>
  </div>
</template>

<script>
export default {
  name: 'app',
  provide () {
    return {
      reload: this.reload
    }
  },
  data () {
    return {
      isRouterAlive: true
    }
  },
  methods: {
    reload () {
      this.isRouterAlive = false
      this.$nextTick(function () {
        this.isRouterAlive = true
      })
    }
  }
}
</script>

<style>
  html,body,#app,.el-container{
    /*0*/
    padding: 0px;
    /**/
    margin: 0px;
    /*100%*/
    height: 100%;
    width:100%;
  }
</style>



