<style scoped>
/deep/
    .el-avatar{
      vertical-align: middle;
    }
    #avatar>label{
      line-height: 43px;
    }

    #personal{
      padding-right: 10px;
      border-right: 1px solid #3C3C3C;
    }
    #personal_sub{
      margin-top: 150px;
    }
    .avatar{
      width: 100px;
      height: 100px;
      border-radius: 100px;
    }
    .avatar_div{
      margin-bottom: 15px
    }
    .avatar-uploader{
      text-align: center;
    }
</style>
<template>
<el-row>
  <el-col :span="12" id="personal">
  <h2 style="text-align:center"></h2>
  <div class="avatar_div">
        <el-upload
            class="avatar-uploader"
            :action="action"
            :headers="token"
            :data='dataList'
            :show-file-list="false"
            :on-success="handleAvatarSuccess"
            :before-upload="beforeAvatarUpload">
            <img v-if="circleUrl" :src="circleUrl" class="avatar">
            <!-- <span v-if="circleUrl" class="el-upload-action" @click.stop="handleRemove()">
                <i class="el-icon-delete"></i>
            </span> -->
            <i v-else class="el-icon-upload2 avatar-uploader-icon" stop></i>
        </el-upload>
  </div>
  <el-form ref="form" :model="form" :rules="addRules" label-width="80px" size="mini" v-loading="loading">
  <!-- <el-form-item id="avatar" label="">
      <el-avatar size="large" :src="circleUrl"></el-avatar>
      <el-button @click="editAvatar"></el-button>
  </el-form-item> -->
  <el-form-item label="" prop="username">
    <el-input v-model="form.username"></el-input>
  </el-form-item>
  <el-form-item label="" prop="password">
    <el-input v-model="form.password" show-password></el-input>
  </el-form-item>
  <el-form-item label="" prop="email">
    <el-input v-model="form.email"></el-input>
  </el-form-item>
  <el-form-item label="" prop="role">
    <el-input v-model="form.role" disabled></el-input>
  </el-form-item>
  <el-form-item label="">
    <el-switch v-model="form.is_black" disabled></el-switch>
  </el-form-item>
  <el-form-item label="">
    <el-switch v-model="form.is_enable" disabled></el-switch>
  </el-form-item>
  <el-form-item label="">
    <el-radio-group v-model="form.sex">
      <el-radio :label="1"></el-radio>
      <el-radio :label="2"></el-radio>
      <el-radio :label="3"></el-radio>
    </el-radio-group>
  </el-form-item>
  <el-form-item id="personal_sub">
    <el-button type="primary" @click="onSubmit"></el-button>
    <el-button></el-button>
  </el-form-item>
</el-form>
  </el-col>
</el-row>
</template>

<script>
import {getHome,getUserInfo,updateUserInfo} from '../../global/api.js';
var $this = {};
export default {
  inject:['reload'],
  data() {
    return {
      token:{'Authorization':"Bearer " + $this.$store.state.token},
      circleUrl: $this.globalBaseUrl + '/' + JSON.parse(this.$store.state.userInfo) .avatar,
      count:0,
      loading:false,
      action:'',
      form: {
          username: '',
          password: '',
          email:'',
          is_enable: false,
          sex:1,
          is_black: false,
          role:''
      },
      // 
      addRules: {
        username : [
          {
            required: true,
            message: '', 
            trigger: 'blur'
          },
        ],
        password : [
          {
            required: true,
            message: '', 
            trigger: 'blur'
          },
        ],
        email : [
          {
            required: true,
            message: '', 
            trigger: 'blur'
          },
          {
            type: 'email', 
            message: '', 
            trigger: ['blur', 'change']
          }
        ]
      },
      dataList:{
        name:'file',
        type:'avatar',
        other:JSON.stringify({id:this.$store.state.userId})
      }
    }
  },
  beforeCreate() {
    $this = this;
  },
  created(){
    $this.action = $this.globalBaseUrl + '/' + 'upload'
    getUserInfo(this.$store.state.userId).then(res => {
      $this.id = res.data.id
      $this.form.username = res.data.user_name
      $this.form.password = res.data.password
      $this.form.role = res.data.role_name
      if(res.data.is_use == 1){
          $this.form.is_enable = true
      }else{
          $this.form.is_enable = false
      }
      if(res.data.is_black == 1){
          $this.form.is_black = true
      }else{
          $this.form.is_black = false
      }
      $this.form.email = res.data.email
      $this.form.sex = parseInt(res.data.sex)
   });
  },
  watch : {
      // 
      form:{
        handler(){
          this.count++
          console.log(this.count);
        },
        deep: true,
        immediate: false
      },
  },
  methods: {
    onSubmit() {
        if(this.count === 1){
          this.$message({
            message  : '',
            type     : "error",
            duration : 1500,
          })
          return
        }
      this.form.id = $this.id
      let editForm = this.form
      updateUserInfo(editForm).then(res => {
          this.$message({
            message  : '',
            type     : "success",
            duration : 1500,
          })
          this.$forceUpdate()
      })
    },
    // 
    handleRemove() {
      this.circleUrl = ''
    },
    // 
    handleAvatarSuccess(res, file) {
      // 
      this.circleUrl = $this.globalBaseUrl + '/' + res.data.url
      // 
      let str = JSON.parse(this.$store.state.userInfo)
      str.avatar = res.data.url
      this.$store.commit('setUserInfo',JSON.stringify(str))
      this.reload();
    },
    // 
    beforeAvatarUpload(file) {
      const type = file.type === 'image/jpeg' || 'image/jpg' || 'image/webp' || 'image/png'
      const isLt5M = file.size / 1024 / 1024 < 5
      if (!type) {
        this.$message.error('!(jpgpngwebpJPEG)')
      }
      if (!isLt5M) {
        this.$message.error(' 5MB!')
      }
      return type && isLt5M
    }
  }
}
</script>


